// File: RatingComponent.js

import React, { useState, useEffect } from "react";
import axios from "axios";
import "./Rating.css";
import images from "../../assets/assets";

const Rating = ({ itemId }) => {
  const [rating, setRating] = useState(null);
  const [product, setProduct] = useState(null);

  useEffect(() => {
    // Gi API  ly thng tin sn phm da trn itemId
    const fetchProduct = async () => {
      try {
        const response = await axios.get(`http://localhost:8080/product/get-product/${itemId}`);
        setProduct(response.data);
      } catch (error) {
        console.log(error);
      }
    };

    // Gi API  ly thng tin nh gi ca sn phm da trn itemId
    const fetchRating = async () => {
      try {
        const response = await axios.get(`http://localhost:8080/rate/get-rate/${itemId}`);
        setRating(response.data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchProduct();
    fetchRating();
  }, [itemId]);

  const handleCreateRating = async () => {
    // Gi API  to nh gi cho sn phm
    const data = {
      user_id: 123, // Thay i thnh user_id tng ng
      item_id: itemId,
      rate: 5, // Thay i thnh nh gi tng ng
      comment: "Great product", // Thay i thnh bnh lun tng ng
      title: "Excellent" // Thay i thnh tiu  tng ng
    };

    try {
      await axios.post("http://localhost:8080/rate/create-rating", data);
      // To nh gi thnh cng, lm cc x l tng ng (nu cn)
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
    <div id="cot_menu" className="menu">
                  <div id="avatar" style={{width: '50px', display: 'inline-block', verticalAlign: 'top', backgroundColor: '#F11B1F', height: '50px', borderRadius: '50%', overflow: 'hidden'}}>
                    <img src={images['./bach.jpg']} alt="Avatar" style={{width: '100%', height: '100%', display: 'inline-block'}} />
                  </div>
                  <div id="menu_1" style={{display: 'inline-block', verticalAlign: 'top', border: '#F9EAEA 0px none', height: '50px', marginBottom: '20px', marginLeft: '20px'}}>Ti khon ca <br />
                    <strong>Bch V</strong>
                  </div>
                  <div id="menu_2" style={{ height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./thongtintaikhoan.png']} alt="Avatar" />Thng tin ti khon
                  </div>
                  <div id="menu_3" style={{height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./thongbaocuatoi.png']} alt="Avatar" />Thng bo ca ti </div>
                  <div id="menu_4" style={{height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./quanlydonhang.png']} alt="Avatar" />Qun l n hng </div>
                  <div id="menu_5" style={{height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./quanlydoitra.png']} alt="Avatar" />Qun l i tr </div>
                  <div id="menu_6" style={{height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./sodiachi.png']} alt="Avatar" />S a ch </div>
                  <div id="menu_7" style={{height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./thongtinthanhtoan.png']} alt="Avatar" />Thng tin thanh ton </div>
                  <div id="menu_8" style={{backgroundColor: '#E0E0E0',height: '40px', border: '#F9EAEA 0px none'}}>
                    <img style={{width: '30px', display: 'inline-block', verticalAlign: 'top', height: '30px', borderRadius: '50%', overflow: 'hidden'}} src={images['./danhgiasanpham.png']} alt="Avatar" />nh gi sn phm </div>
                  
                </div>
    <div className="rating-component" >
      {!product ? (
        <p className="no-product-message">Cha c sn phm, hy mua sm</p>
      ) : (
        <>
          <div className="product-info">
            <img src={product.image_url} alt={product.title} className="product-image" />
            <div className="product-details">
              <h3>{product.title}</h3>
              <p className="category">{product.category}</p>
              <p className="price">Gi: {product.price}</p>
              <p className="number-sold"> bn: {product.number_sold}</p>
            </div>
          </div>
          {rating ? (
            <div className="rated-info">
              <h3>nh gi sn phm:</h3>
              <p className="rate-label">nh gi:</p>
              <p className="rate-value">{rating.rate}</p>
              <p className="comment-label">Bnh lun:</p>
              <p className="comment">{rating.comment}</p>
              <p className="title-label">Tiu :</p>
              <p className="title">{rating.title}</p>
              <button disabled> nh gi</button>
            </div>
          ) : (
            <div>
              <button className="create-rating-button" onClick={handleCreateRating}>
                Ch nh gi
              </button>
            </div>
          )}
        </>
      )}
    </div>
    </>
  );
};

export default Rating;
